/* Fondo degradado */
body {
  margin: 0;
  padding: 0;
  background: linear-gradient(to bottom, #2c3e50, #1c2833);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Arial', sans-serif;
}

/* Contenedor principal */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Caja del login con efecto de vidrio */
.login-box {
  background: rgba(255, 255, 255, 0.1);
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  text-align: center;
  backdrop-filter: blur(10px);
  width: 350px;
}

/* Logo de la farmacia */
.login-logo {
  width: 120px;
  margin-bottom: 15px;
}

/* Título */
h2 {
  color: white;
  margin-bottom: 20px;
}

/* Inputs */
.input-group {
  margin-bottom: 15px;
}

.input-group input {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 5px;
  outline: none;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  font-size: 16px;
}

.input-group input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

/* Recordar sesión y olvidar contraseña */
.remember-me {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  margin-bottom: 20px;
  color: white;
}

.remember-me a {
  color: #3498db;
  text-decoration: none;
}

/* Botón de inicio de sesión */
.login-button {
  background: linear-gradient(to right, #8e44ad, #3498db);
  border: none;
  padding: 12px;
  width: 100%;
  border-radius: 5px;
  color: white;
  font-size: 18px;
  cursor: pointer;
  transition: background 0.3s;
}

.login-button:hover {
  background: linear-gradient(to right, #6f298e, #2574a9);
}
